/* Fields */

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea {
	color: #666;
	border: 1px solid #ccc;
	border-radius: 3px;

	&:focus {
		color: #111;
	}
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"] {
	padding: 3px;
}

textarea {
	padding-left: 3px;
	width: 100%;
}
// find us page
.find-us-section {
    margin: 1.5rem 0 0;
    width: 1120px;
    margin-left: auto;
    margin-right: auto;
    // .find-us-content{

    // }
    h2 {
        color: #2F1339;
        font-size: 2.1rem;
        margin-bottom: 1.5rem;
        line-height: 1.25;
        font-family: Novecento-Bold;
        text-transform: uppercase;
    }
    p {
        margin-bottom: 1.5rem;
        font-family: Merriweather-Light;
    }

    form {
        label {
            color: #2f1339;
            display: block;
            font-family: Novecento-Normal;
            font-size: 1.1rem;
            margin-bottom: 0.5rem;
            text-transform: uppercase;
        }
    .required {
        color: #d99054;
    }
    input {
        color: #404040;
        font-family:  Merriweather-Light;
        font-size: 1rem;
        line-height: 1.75;
    }
    input[type=text] {
        padding: 0.3rem;
        color: #666;
        background-color: #f4f4f4;
		border: 1px solid #e1e1e1;  
		width: 30%;
    }
    .message {
        input {
            padding-left: 0.3rem;
           height: 300px;
           width: 75%;
        }
	}
	#submit {
		background: #248a83;
    border: none;
    color: #FFF;
    font-family: Novecento-Normal;
    line-height: 1;
    padding: 0.75rem 1.5rem;
    text-decoration: none;
    text-transform: uppercase;
	// width: 19%;
	margin-bottom: 6%;
    &:hover {
        background: #19625d;
        color: #FFF;
    }
	}
    }
