a {
    // color: #248A83;
    &:hover, &:active {
        color: #F0E4D1;
    }
    &:focus {
        outline: thin dotted;
    }
    &:hover, &:active {
        outline: 0;
    }
}

.site-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    z-index: 3;
    transition: top 0.5s linear;    
    .header-container {
        display: flex;
        justify-content: space-between;
        width: 1120px;
        margin-left: auto;
        margin-right: auto;
        .main-navigation {
            font-family: Novecento-Normal;
            li {
                padding-right: 1.5rem;
            }
            display: flex;
            align-items:center; 
        }
        .search-form {
            fieldset {
                border: 0;
                padding: 0;
                margin: 0;
            label {
                display: none;
            }
            }
        }
    }
    h1 {
        background: url(../../assets/images/logos/inhabitent-logo-tent.svg) no-repeat;
        background-size: 50px 33px;
        height: 33px;
        margin: 0;
        overflow: hidden;
        text-indent: 100%;
        width:50px;
        white-space: nowrap;
        //width: 50px;
    }
    &.reverse-header {
        background-color: transparent;
        position: absolute;
        text-shadow: 1px 1px 2px #323232;
        align-items: center;
        h1 {
            background: url(../../assets/images/logos/inhabitent-logo-tent-white.svg) no-repeat;
            background-size: 50px 33px;
        }
        a {
            color: #FFF;
            text-transform: uppercase;
            &:hover {
                color: #F0E4D1;
            }
            &:visited {
                color: #FFF;
            }
            }
        
    
    }
        &.scroll-header {
            h1 {
                background: url(../../assets/images/logos/inhabitent-logo-tent.svg) no-repeat;
                background-size: 50px 33px; 
            }
            // background-color: hsla(0,0%,100%,0.85);
            border-bottom: 1px solid #c1c1c1;
            padding: 0.5rem 0;
            position: fixed;
            transition: top 0.5s linear;
            width: 100%;
            z-index: 3;
            a {
                color: #248a83;
            }
        }
}


.scroll-header {
    h1 {
        background: url(../../assets/images/logos/inhabitent-logo-tent.svg) no-repeat;
    }
}


.main-navigation {
    display: flex;
    align-items: center;
